import React from "react";
import data from "./poolData.json";
import Select from "react-select";
import { Bar } from "react-chartjs-2";
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend,
} from "chart.js";
import "./App.css";
import { exitChances } from "./consts";

ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend
);


function App() {
    const [equityVal, setEquityVal] = React.useState();
    const [investRound, setInvestRound] = React.useState();

    function calcPoolExitChances(poolRound) {
        const loosingChances = Math.pow((1 - exitChances[poolRound]), data[poolRound]?.length) * exitChances[investRound]
        return (1 - loosingChances)
    }

    function chartData(round) {
        return {
            labels: ["Pool", "Without Pool"],
            datasets: [
                {
                    label: "Exit Chances",
                    data: [calcPoolExitChances(round), exitChances[investRound]],
                    backgroundColor: [
                        "#ffbb11",
                        "#ecf0f1",
                        "#50AF95",
                        "#f3ba2f",
                        "#2a71d0",
                    ],
                },
            ],
        }
    };
    return (
        <div className="App">
            <div className="container">
                <div className="form-wrapper">
                    <div>
                        <span className="form-text">Equity Value</span>
                        <br />
                        <input
                            className="form-input"
                            type="number"
                            placeholder={0}
                            name="equityVal"
                            onChange={(e) =>
                                setEquityVal(Number(e.target.value))
                            }
                            value={equityVal || ""}
                        />
                    </div>
                    <div>
                        <span className="form-text">Round Investment</span>
                        <br />
                        <Select
                            className="form-select"
                            options={[
                                { value: "seed", label: "Seed" },
                                { value: "a", label: "A" },
                                { value: "b", label: "B+" },
                                { value: "unicorn", label: "Unicorn" },
                            ]}
                            onChange={(round) => setInvestRound(round.value)}
                        />
                    </div>
                </div>
                <div className="charts-container">
                    <div>
                    <div className="chart-wrapper">
                        <Bar
                            data={chartData("seed")}
                            options={{
                                plugins: {
                                    title: {
                                        display: true,
                                        text: "Round Seed",
                                    },
                                    legend: false
                                },
                            }}
                        />
                    </div>
                    <div className="chart-wrapper">
                        <Bar
                            data={chartData("a")}
                            options={{
                                plugins: {
                                    title: {
                                        display: true,
                                        text: "Round A",
                                    },
                                    legend: false
                                },
                            }}
                        />
                    </div>
                    </div>
                    <div>
                    <div className="chart-wrapper">
                        <Bar
                            data={chartData("b")}
                            options={{
                                plugins: {
                                    title: {
                                        display: true,
                                        text: "Round B+",
                                    },
                                    legend: false
                                },
                            }}
                        />
                    </div>
                    <div className="chart-wrapper">
                        <Bar
                            data={chartData("unicorn")}
                            options={{
                                plugins: {
                                    title: {
                                        display: true,
                                        text: "Unicorns",
                                    },
                                    legend: false,
                                },
                            }}
                        />
                    </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default App;
